
    .content{
        margin-top: 16px;
    }
    .inp{
        border: none;
        padding: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
        border-radius: 10px;
    }
    .inp:focus{
        outline: none;
    }
    .submitbtn{
        border: none;
        padding: 10px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
        border-radius: 10px;
        background-color: rgb(77, 66, 229);
        color: whitesmoke;
    }
    
    .list{
        padding: 10px;
        background-color: gray;
        color: whitesmoke;
        display: inline-block;
        margin-top: 8px;
        border-radius: 16px;
    }
    .deletebtn{
        border: none;
        padding: 6px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
        border-radius: 10px;
        background-color: rgb(225, 39, 110);
        color: whitesmoke;
    }